  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  color: #424242;
}

img {
  display: block;
}

/* skip to content */
.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: #bf1722;
  color: white;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

/* Header */
.header {
  min-height: 60px;
  background-color: white;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.12), 0 4px 8px rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
}

.header__inner {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
}

.header__logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.header__title a {
  text-decoration: none;
  font-size: 37px;
  color: #424242;
}

.nav__list {
  width: 100%;
  padding: 0;
  margin: 0;
  text-align: center;
}

.nav__item {
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
  line-height: 24px;
  text-transform: uppercase;
}

.nav a {
  display: inline-block;
  padding: 1.3em;
  text-decoration: none;
  color: #616161;
  font-size: 16px;
  font-weight: bolder;
  min-width: 44px;
  max-height: 44px;
}

.nav a:hover {
  text-decoration: underline;
  color: #000;
}

#drawer__mobile {
  display: none;
}

#hamburger {
  font-size: 50px;
  text-decoration: none;
  border: none;
  background-color: white;
  display: inline-block;
  min-width: 44px;
}

/* hero */
.hero {
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-color: green;
}

.hero__inner {
  margin: 0 auto;
  max-width: 800px;
}

.hero__title {
  color: #fff;
  font-weight: 500;
  font-size: 30px;
}

.hero__tagline {
  color: #fff;
  margin-top: 16px;
  font-size: 18px;
  font-weight: 300;
  font-weight: bold;
}

/* main */
main {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-repeat: no-repeat;
  background-size: cover;
}

/* content */
.content {
  padding: 25px;
}

.content__label {
  font-size: 28px;
  font-weight: bolder;
  text-align: center;
}

.restaurant-item__not__found {
  text-align: center;
}

.restaurants {
  margin: 32px auto auto;
  overflow: auto;
  display: grid;
  gap: 15px;
}

/*
 * post item
 */

.restaurant-item {
  margin: 16px 0;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
}

.restaurant-item__content {
  padding: 16px 32px 32px 32px;
  background-color: white;
  height: 100%;
}

.restaurant-item__thumbnail {
  width: 100%;
  height: 300px;
}

.restaurant-item__date {
  font-size: 10px;
  text-transform: uppercase;
  color: #999;
  font-size: 20px;
}

.restaurant-item__date__author {
  color: red;
  text-decoration: none;
}

.restaurant-item__title {
  font-weight: 500;
  font-size: 24px;
  margin-top: 16px;
  transition: 0.3s opacity;
}

.restaurant-item__title:hover {
  opacity: 0.5;
}

.restaurant-item__title a {
  text-decoration: none;
  color: inherit;
  font-size: 37px;
  display: inline-block;
  min-width: 44px;
  max-height: 44px;
}

.restaurant-item__description {
  margin-top: 16px;
  font-size: 14px;
  line-height: 1.5em;
}

/* detail */
.restaurant {
  margin: 0 auto;
  width: 100%;
  max-width: 1000px;
  /* display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px; */
}

.restaurant__title {
  text-align: center;
  font-size: 32px;
  margin-bottom: 20px;
}

.restaurant .restaurant-img__wrapper {
  display: flex;
  justify-content: space-evenly;
  gap: 20px;
}

.restaurant .restaurant__poster {
  width: 100%;
  max-width: 500px;
}

.restaurant-info__wrapper {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  gap: 50px;
}

.restaurant .restaurant__info h4 {
  margin: 8px 0;
  font-size: 18px;
}

.restaurant .restaurant__info p,
span {
  font-size: 16px;
}

.restaurant__description {
  font-size: 24px;
}

/* Like */
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;

  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;

  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* footer */
footer {
  display: flex;
  height: 100px;
  background-color: white;
  align-items: center;
}

.footer__inner {
  padding: 8px;
  width: 100%;
}

.footer__inner h3 {
  color: black;
  text-align: center;
}
